<script>
export default {
  
  name: "CheckOut",
  data() {
    return {
      phivanchuyen: 20000,

      email: "",
      name: "",
      phone: "",
      address1: "",
      address2: "",
      errors: [],
    };
  },
  computed: {
    tongcong() {
      return this.$store.getters.sum + this.phivanchuyen;
    },
  },
  methods: {

    // postOrder(e) {
      
    //   this.errors = [];
      
    //   if (!this.name) {
    //     this.errors.push("Name required .");
    //   }
    //   if (!this.email) {
    //     this.errors.push("Age required.");
    //   }
 
    //   if (this.errors.length > 0) {
    //     e.preventDefault();
    //   }
    //   if (this.errors.length == 0) {
    //     let data = {
    //       email: this.email,
    //       name: this.name,
    //       phone: this.phone,
    //       address1: this.address1,
    //       address2: this.address2,
    //       cart: this.$store.state.cart,
    //     };

    //     this.$store.dispatch("postOrder", data);
    //     // window.location.href = "http://localhost:8080/#/";
    //     this.$router.push({ name: "/" });
    //   }
    // },
    checkForm: function (e) {
      // if (this.name && this.age) {
      //   // e.preventDefault();
      //   console.log('conga')
      //   this.$route.push({to:'/home'})
      //   return true;
      // }
      var phoneno = /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/;
      var re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      this.errors = [];

      if (!this.name) {
        this.errors.push("Name required.");
      }
      if (!this.email) {
        this.errors.push("Age required.");
      }
      if (!phoneno.test(this.phone)) {
        this.errors.push("Not type of Phone. ");
      }
      if (!re.test(this.email)) {
        this.errors.push("Not type of Email. ");
      }
      if (this.errors.length > 0) {
        e.preventDefault();
      }
      if (this.errors.length == 0) {
        let data = {
          email: this.email,
          name: this.name,
          phone: this.phone,
          address1: this.address1,
          address2: this.address2,
          cart: this.$store.state.cart,
        };

        this.$store.dispatch("postOrder", data);

        // window.location.href = "http://localhost:8080/#/checkouted";
        this.$router.push({ name: "checkouted" });
      }
    },
  },
};
</script>

