<template>
  <div class="pagination">
    <div class="pagination-info">
      <h1>Home | Shop {{$route.query.page}}</h1>
    </div>
    
    <div class="collection">
      
        <div class="collection-item">
            <product  class="collection-product" v-for="(product,id) in $store.getters.getItem" :key="id"
            :product="product"
            
            />
        </div>
      <!-- <p>{{parseInt(this.$route.query.page)>3?1+(parseInt(this.$route.query.page)-3):"sai"}}</p> -->
      
      <div class="collection-nav">
            <nav aria-label="Page navigation example">
        <ul class="pagination">
          <li v-if="prePagi ">
            <router-link
              tag="a"
              :to="{ query: { page: parseInt(this.$route.query.page) - 1 }}"
              class="page-link"
            ><i class="fas fa-chevron-left"></i></router-link>
          </li>

          <li  class="page-item" v-for="(todo,idx) in 3" :key="idx" :class="{'activeT': parseInt($route.query.page) == (parseInt($route.query.page) >3?todo+(parseInt($route.query.page)-3) :todo)}">
            <!-- <router-link tag="a" class="page-link" :to="{ query: { page: todo }}">{{checkTo+1>3?todo+1:todo}}</router-link> -->
            <router-link
              tag="a"
              class="page-link"
              :to="{ query: { page: todo }}"
            >{{checkTo>3?todo+(checkTo-3):todo}}</router-link>
          </li>

          <li :class="{'disabled': parseInt(this.$route.query.page) == parseInt(this.$store.getters.perPage)}">
            <router-link
              tag="a"
              :to="{ query: { page: iniPagi  }}"
              class="page-link"
            ><i class="fas fa-chevron-right"></i></router-link>
          </li>
        </ul>
      </nav>
      </div>
    </div>
  </div>
</template>

<script>
import Product from './Product'
export default {
  name: "Pagination",
  components:{
      Product
  },
  data() {
    return {
      arrayTodo: "",
      currentPage: parseInt(this.$route.query.page)
    };
  },
  mounted() {
    this.$store.dispatch("getProductPagination");
  },
  computed: {
    routerParams() {
      if (this.page < 4) {
        return typeof this.page;
      } else {
        return this.page + 1;
      }
    },
    checkTo() {
      return parseInt(this.$route.query.page);
    },
    // neu khong co query.page 
    iniPagi(){
        if(!this.$route.query.page){
            return 2
        }else{
            return parseInt(this.$route.query.page) +1
        }
    },
    // neu khong co query.page Preview
    prePagi(){
        // if(parseInt(this.$route.query.page)!=1){
        //     return true
        // }
        
        if(!this.$route.query.page){
            return false
        }else{
            if(parseInt(this.$route.query.page)!=1){
                return true
            }else{
                return false
            }
        }
    }
    
  },
  watch: {
    "$route.query.page": {
      handler: function(page) {
        if(!this.$route.query.page){
            this.$store.commit("GETITEM", 1);
        }
        else{
            this.$store.commit("GETITEM", page);
        }
        
      },
      deep: true,
      immediate: true
    }
  }
};
</script>

