<template>
  <section id="shop">
   <p v-if="error">this is error</p>
    <div class="item">
       
      <img v-bind:src="product.linkImg" alt />
    </div>
    <div class="info">
      <h1>{{product.name}}</h1>
      
      <h1>{{product.price}}</h1>
      <h3>Quantity</h3>
      <div class="group-button">
        <button @click="$store.commit('addtoCart',product)">Add to cart</button>
        <button>Buy It Now</button>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
export default {
  name: "Shop",
  data(){
      return{
          product :'',
          error :null

      }
  },
 
watch:{ 
     '$route.params.id': {
        handler: function(id) {
           axios.get(`/api/item/${id}`)
           .then(res => this.product = res.data)
           .catch(error =>this.error = error)
        },
        deep: true,
        immediate: true
      }
}

};
</script>

