import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
const url = 'api/'
function localStorageCart(cart) {
    localStorage.setItem('cart', JSON.stringify(cart));
}
export default new Vuex.Store({
    state: {
        items: [
            { id: 1, name: "quan", des: "depnha", price: 1000 },
            { id: 2, name: "ao", des: "depnha", price: 1000 },
            { id: 3, name: "ao thun", des: "depnha1 ", price: 1000 }
        ],
        products: [],

        cart: [],
        successOrder: '',
        todopagi: [],
        page: '',


    },
    getters: {
        quantityCart(state) {
            let sum = 0
            for (let i = 0; i < state.cart.length; i++) {
                sum += state.cart[i].quantity
            }
            return sum;
        },
        sum(state) {
            let sum = 0
            for (let i = 0; i < state.cart.length; i++) {
                sum += state.cart[i].quantity * state.cart[i].price
            }
            return sum
        },
        perPage(state) {
            if (state.todopagi.length % 8 == 0) {
                return state.todopagi.length / 8;
            } else {
                return state.todopagi.length / 8 + 1
            }
            // return state.todopagi.length/8;
        },
        getItem: (state) => {
            let begin = (state.page - 1) * 8;
            let end = (state.page - 1) * 8 + 8;
            return state.todopagi.slice(begin, end)
        }
    },
    actions: {
        //1
        getProduct({ commit }) {
            // axios(url + 'item/getProductMain')
            axios.get(`api/item/getProductMain`)

                .then(response => {
                    commit('GETPRODUCTMAIN', response.data)
                })

        },


        postOrder({ commit }, data) {

            axios.post(url + 'order/postOrder', data)
                .then(function (response) {

                    commit('POSTORDER', response.data)
                })

        },
        //1
        getProductPagination({ commit }) {
            axios(url + 'item/getItem')
                .then(response => {
                    commit('GETPRODUCTPAGINATION', response.data)
                })

        },


    },
    mutations: {
        GETPRODUCTMAIN(state, productMain) {
            state.products = productMain
        },

        addtoCart(state, product) {
            let x = state.cart.find(i => i._id == product._id)
            if (x) {

                x.quantity++
            } else {

                state.cart.push({ ...product, quantity: 1 })
            }
            localStorageCart(state.cart);
        },

        addlocalStorage(state) {

            let x = localStorage.getItem('cart');
            if (x) {
                state.cart = JSON.parse(x)
            }
        },


        removeFromCart(state, product) {
            let item = state.cart.find(i => i._id == product._id)
            //    let x = state.cart.find(i =>i._id == product._id)
            if (item) {
                if (item.quantity > 1) {
                    item.quantity--
                } else {
                    state.cart = state.cart.filter(i => i._id !== product._id)
                }
            }
            localStorageCart(state.cart);
        },
        POSTORDER(state, data) {
            state.cart = 0;
            localStorage.removeItem('cart');
            // localStorageCart(state.cart);
            // localStorageCart(state.cart);
            state.successOrder = `ban da dat hang thanh cong ${data._id}`
        },
        GETPRODUCTPAGINATION(state, data) {
            state.todopagi = data
        },
        GETITEM(state, data) {
            state.page = data
        },


    }
})